@import url(http://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);

.App {
  font-family: "Open Sans", "Helvetica";
  font-weight: lighter;
  font-size: 14px;
  color: #555;
  line-height: 150%;
  margin-top: 100px;
}

.App-header {
  margin-top: 50px;
}

.App-header-container {
  margin: 0 auto 80px auto;
  padding: 20px 10%;
  max-width: 800px;
  min-height: 35px;
}

.header-left {
  float: left;
  display: block;
}

.header-right {
  float:right;
  display: block;
  text-align: right;
}

.page-title {

}

.first-name {
  font-size: 52px;
  color: #555;
  margin-bottom: 20px;
}

.phoneme {
  font-size: 24px;
  color: #999;
}

.last-name {
  font-size: 28px;
  color: #3778BA;
}

.App-body {
  margin: 0 auto;
  padding: 0 10%;
  max-width: 800px;
}

.left {
  display: block;
  float: left;
}

.right {
  display: block;
  float: right;
}

h1 {
  font-size: 32px;
  color: #555;
  font-weight: lighter;
  clear: both;
  line-height: 150%;
}

h2 {
  font-size: 20px;
  color: #3778BA;
  font-weight: lighter;
  margin-top: 5px;
}

h3 {
  font-size: 16px;
  color: #555;
  margin: 5px 0;
  font-weight: lighter;
}

.top-menu-container {
  width: 100%;
  background-color: #333;
  height: 40px;
  position: fixed;
  z-index: 10;
  padding-top: 15px;
  top: 0;
}

.top-menu {
  margin: 0 auto;
  max-width: 800px;
  text-align: right;
}

.top-menu li {
  display: inline;
  margin-right: 10px;
}

.top-menu-item,
.top-menu-item:visited {
  color: #aaa;
  text-decoration: none;
}
.top-menu-item:hover {
  color: #3778BA;
  text-decoration: none;
}

.button-cv {
  background-color: #3778BA;
  border: none;
  font-family: "Open Sans", "Helvetica";
  font-weight: lighter;
  font-size: 14px;
  color: #ddd;
  text-decoration: none;
  padding: 6px 12px;
  border-radius: 2px;
  opacity: 0.9;
}
.button-cv:hover {
  opacity: 1;
  cursor: pointer;
}

.about-me {
  margin-top: 50px;
}

.description {
  color: #999;
  line-height: 130%;
}

.description-below {
  margin: 0 0 10px;
}

.description-above {
  margin: 10px 0 0;
}

.skills {
  width: 48%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.year {
  color: #3778BA;
}

.working-experience-item,
.education-item {
  margin-top: 30px;
}

.working-period,
.studying-period {
  color: #999;
}

.company-name,
.institution-name {
  font-size: 16px;
}

.working-experience-description,
.education-description {
  display: block;
  clear: both;
}

.responsibilities {
  margin: 20px 0 10px 0;
}

/** Keep this at the end **/
@media print {
  .top-menu-container {
    display: none;
  }
  @page {
    margin: 2cm 0.5cm 2cm 0.5cm;
  }
  @page :first {
    margin: 0 0.5cm 2cm 0.5cm;
  }
}


